import {Component,OnInit} from '@angular/core';
import { Routes,Route,Router,ROUTER_DIRECTIVES } from '@angular/router';

import {MdButton, MdAnchor} from '../components/button/button';
//import {MdIcon} from '../components/icon/icon';
//import {MD_LIST_DIRECTIVES} from '../components/list/list';

import { HeroPortalComponent } from './hero-portal/hero-portal.component';
import { TodoPortalComponent } from './todo-portal/todo-portal.component';
import { DownupAnimationComponent } from './xxj-animation/downup-animation.component';
import { DemoApp } from '../demo-app/demo-app';

export class Element {
    constructor(private nodeName: string, private parent?: Element) {
        
    }
    toString() {
        return '<' + this.nodeName + '>';
    }
};

@Component({
    selector:'my-app',
    template:`
             <md-nav-list class="xxj-md-nav-list">
                <md-list-item class="xxj-md-list-item">
                    <a [routerLink]="['hero-portal']"  
                       md-raised-button color="primary"
                       class="xxj-md-raised-button">Hero-Portal</a> 
                </md-list-item>
                <md-list-item class="xxj-md-list-item">
                    <a [routerLink]="['todo-portal']"  
                       md-raised-button color="primary"
                       class="xxj-md-raised-button">Todo-Portal</a> 
                </md-list-item>
                <md-list-item class="xxj-md-list-item">
                    <a [routerLink]="['xxj-animation']"  
                    md-raised-button color="primary"
                    class="xxj-md-raised-button">DownupAnimation</a> 
                </md-list-item>
                <md-list-item class="xxj-md-list-item">
                    <a [routerLink]="['demo-app']"  
                    md-raised-button color="primary"
                    class="xxj-md-raised-button">M2 Demo</a> 
                </md-list-item>
             </md-nav-list>
             
             <div>
             <router-outlet></router-outlet>
             </div>
             <hr>
    `,
    styleUrls:["app/app.component.css"],
    host:{
          'margin-top':'1em',
          'position':"absolute",
          "bottom":"1%"
        },
    directives:[ROUTER_DIRECTIVES,MdButton, MdAnchor]
})
@Routes([
new Route({ path:'/hero-portal', component:HeroPortalComponent}),
new Route({ path:'/todo-portal', component:TodoPortalComponent}),
new Route({ path:'/xxj-animation', component:DownupAnimationComponent}),
new Route({ path:'/demo-app', component:DemoApp})
])

export class AppComponent implements OnInit {
    title='This is Home';
    constructor(private _r:Router){}
    ngOnInit(){
        this._r.navigate(['/hero-portal']);
    }
}

